@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
html {
    font-size: 62.5%;
}
body {
    font-family: 'Quicksand', sans-serif;
    overflow: hidden;
}
.container {
    display: flex;
}

.container .audio {
    width: 100%;
    min-height: 100vh;
    padding: 120px 0;
    color: rgb(255, 255, 255);
    text-align: center;
}
.background {
    background-image: url(/playAudio/static/media/background.4e165c79.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    position: absolute;
    z-index: -1;
    top: -2rem;
    left: -2rem;
    width: 105%;
    min-height: 130vh;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.Song-Thumbnail {
    width: 25rem;
    height: 25rem;
    border-radius: 100%;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.555);
}

.Song-Title {
    text-transform: capitalize;
    margin-bottom: 0;
    font-size: 2rem;
    margin: 1rem 0 1rem 0;
    padding: 0 1rem;
}

.Singer {
    margin-top: 0;
    margin-bottom: 2rem;
    color: rgb(253, 253, 253);
    font-size: 1.8rem;
}

.Control-Button-Group {
    display: flex;
    align-items: center;
    justify-content: center;
}
.Control-Button-Group > * {
    cursor: pointer;
}
.Control-Button-Group svg {
    font-size: 3rem;
}

.Pause-Play-Button {
    display: flex;
    justify-content: center;
    margin: 0 32px;
}
.render-time{
  font-size: 1.5rem;
  margin-top: 1rem;
}

/* SIDEBAR */
.container .slide-bar {
    position: fixed;
    right: 0;
    overflow: auto;
    color: #ffffff;
    width: 30rem;
    background: #451149b8;
    height: 100%;
}
.container .slide-bar > h1 {
    text-align: center;
    padding: 2rem;
}
.container .open-sidebar,
.container .close-sidebar {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0.5rem;
    padding: 0.5rem;
    transition: 0.4s all ease;
}
.container .open-sidebar {
    right: 1.5rem;
}
.container .close-sidebar {
    left: 0.5rem;
}
.container .open-sidebar:hover,
.container .close-sidebar:hover {
    background-color: rgb(222, 124, 52);
    border-radius: 50%;
    transition: 0.4s all ease;
}
.container .open-sidebar svg,
.container .close-sidebar svg {
    font-size: 4rem;
    color: #ffffff;
    cursor: pointer;
}
.container .slide-bar.close {
    right: -30rem;
    transition: 0.5s all ease;
}
.container .slide-bar.open {
    right: 0;
    transition: 0.5s all ease;
}
.slide-bar .button-add-audio {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}
.slide-bar .button-add-audio > button {
    font-size: 1.1rem;
}

/* SLIDER */
.css-wpz5lw-Slider {
    height: 0.4rem !important;
    width: 50% !important;
    margin-top: 2rem;
}
.css-3uoig3-Slider {
    position: relative;
    display: block;
    width: 15px !important;
    height: 15px !important;
    background-color: #fff !important;
    border-radius: 50% !important;
    margin-top: 0 !important;
}
.css-6nna9v-Slider {
    background-color: #fff !important;
}

/* effect image*/
.playAudio {
    -webkit-animation: spin 30s infinite linear;
            animation: spin 30s infinite linear;
}
@-webkit-keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.audio-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: 0.4s all ease;
    cursor: pointer;
    width: 100%;
    padding: 1rem;
}
.audio-item:hover,
.audio-item.playing {
    background: #a93ab1f3;
    transition: 0.4s all ease;
}
.audio-item > img {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
}

.audio-item > p {
    text-align: center;
    width: 100%;
    padding: 1rem;
    font-size: 1.2rem;
}

.form-upload-audio {
    position: absolute;
    top: -40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgba(89, 25, 79, 0.94);
    width: 30rem;
    padding: 2rem 2rem 4rem 2rem;
    color: #ffffff;
    font-size: 1.8rem;
    text-align: center;
    border-radius: 2rem;
    transition: 0.5s all ease-out;
    box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.438);
}
.form-upload-audio > .input-file {
    padding: 2rem 0 .7rem 0;
}
.form-upload-audio > button{
    margin-top: 1rem;
    float: right;
}
.form-upload-audio.open {
    top: 40%;
    transition: 0.5s all ease-out;
}
.form-upload-audio .MuiFormControl-root {
    width: 100%;
    margin-bottom: 1rem;
}
.form-upload-audio .MuiFormControl-root > label {
    color: #ffffff;
    font-size: 1.3rem;
}
.form-upload-audio .MuiFormControl-root input {
    color: #ffffff;
    font-size: 1.5rem;
}
.form-upload-audio .icon-close-form {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    cursor: pointer;
}
.form-upload-audio .icon-close-form svg {
    font-size: 3rem;
    color: #ffffff;
}

